<template>
    <div class="card">
        <p></p>
        <div class="searchBar">
            <input type="number" v-model="studentNo" placeholder="">
        </div>
        <div class="searchBtn">
            <button @click="fetchNow"></button>
        </div>
    </div>
    <div class="record" v-if="record">
        <div class="fetched" v-if="fetch">
            <p class="tips">_(:)_ </p>
            <p class="msg">{{ result.studentName }}</p>
            <p class="msg">{{ result.studentNo }}</p>
            <p class="msg"></p>
        </div>
        <div class="fetched" v-else>
            <p class="tips">() </p>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            record: false,
            fetch: true,
            studentNo: '',
            result: {
                studentName: '',
                studentNo: ''
            }
        }
    },
    methods: {
        fetchNow () {
            if (isNaN(parseInt(this.studentNo))) {
                window.alert('10')
                return
            }
            this.$http.get('http://szdx.sinaapp.com/api/fetchcard.php?studentNo=' + this.studentNo)
                .then(response => {
                    if (!response.data) {
                        this.record = true
                        this.fetch = false
                        return
                    }
                    this.record = true
                    this.fetch = true
                    this.result = response.data
                })
        }
    }
}
</script>

<style lang="less" scoped>
body {
    font-family: Helvetica, sans-serif;
}

.card {
    float: left;
    width: 100%;
    margin-top: 50px;
    
    p {
        color: #408ec0;
        font-size: 20px;
        font-weight: bold;
        line-height: 80px;
        text-align: center;
    }
    .searchBar {
        float: left;
        padding: 0 5px 0 40px;
        width: 70%;
        text-align: right;

        input {
            width: 100%;
            height: 30px;
            padding-left: 5px;
            color: #408ec0;
            font-size: 14px;
            line-height: 30px;
            border: 1px solid #408ec0;
            border-radius: 2px;
        }
    }
    .searchBtn {
        float: left;
        width: 30%;
        padding: 0 40px 0 5px;

        button {
            width: 100%;
            height: 30px;
            color: #FFF;
            font-size: 14px;
            line-height: 30px;
            border-radius: 2px;
            background: #408ec0;
        }
    }
}

.record {
    float: left;
    width: 100%;
    padding: 20px 40px;

    .tips {
        margin-bottom: 20px;
        text-align: center;
        font-size: 14px;
        line-height: 30px;
        color: #F45757;
    }
    .msg {
        text-align: center;
        font-size: 14px;
        line-height: 24px;
        color: #444;
    }
}
</style>



